<template>
  <div
    id="cleaningTaskTop"
    :style="{'top':$route.query.userId?'50px':'103px'}"
  >
    <!-- 月份 -->
    <div>
      <div class="task-top">
        <div
          @click="time_show = true"
          class="top-left"
        >
          <span>{{ cYearM }}</span>
          <van-icon name="arrow-down" />
        </div>
        <div>
          <mt-navbar
            v-model="selected"
            @click.native="getCurrentInfo()"
          >

            <mt-tab-item
              v-if="!$route.query.userId"
              id="0"
            >
              <span>全部</span>
            </mt-tab-item>
            <mt-tab-item id="1">
              <span>退房打扫</span>
            </mt-tab-item>
            <mt-tab-item id="2">
              <span>续住打扫</span>
            </mt-tab-item>
          </mt-navbar>
        </div>
      </div>
      <div class="totleAll">
        <div>
          共完成<span class="c_00 f18">{{ totleNum }}</span>单{{ '清洁' }}任务，合计收入
        </div>
        <div class="c_00 f20">{{ priceAll || 0 | filterAmount }}</div>
      </div>
      <!-- 开始 -->
      <div
        class="task-list"
        :key="contentKey"
      >
        <div>
          <div
            v-for="(item, i) in listData"
            :key="i"
          >
            <div
              class="task"
              @click="item.status = !item.status"
            >
              <div class="task-left">
                <div
                  class="g_fw"
                  style="font-size:15px;"
                >{{ item.time | dataFilter('MM月DD日') }}</div>
                <div class="f12">{{ '共完成'+ item.child.length +'单' }}</div>
              </div>
              <div class="task-right task-right-top">
                <div>{{ item.totlePrice | filterAmount }}</div>
                <van-icon
                  style='margin-left:5px;'
                  :name="item.status == false?'arrow':'arrow-down'"
                />
              </div>
            </div>
            <div
              v-if="item.status"
              :key="key"
            >
              <div
                v-for="(_child ,index) in item.child"
                :key='index'
                class="task"
              >
                <div class="task-left">
                  <div>{{ _child.roomScale.scaleName }}&ensp;{{ _child.roomSn }}&ensp;<span
                      v-if="!$route.query.userId && selected == '0'"
                      style="font-size:12px;"
                      :class="_child.cleanOrderType==1?'go':'back'"
                    >{{ _child.cleanOrderType==1?'续住':'退房'}}</span></div>
                  <div class="f12">{{ _child.endTime.substr(11) }}&ensp;<span v-if="!$route.query.userId && _child.sysUser">{{ _child.sysUser.realName }}</span></div>
                </div>
                <div
                  class="task-right"
                  @click="editPrice(_child)"
                >
                  <div>
                    <div>{{ _child.cleanAmount | filterAmount }}</div>
                    <div
                      v-if="_child.description"
                      class="f12"
                    >备注：{{ _child.description }}</div>
                  </div>
                  <van-icon
                    style='margin-left:5px;'
                    name="arrow"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 结束 -->
    <van-popup
      position="bottom"
      v-model="time_show"
    >
      <van-datetime-picker
        class="timeBox"
        type="year-month"
        v-model="currentDate"
        :max-date="maxDate"
        @cancel="time_show = false"
        @confirm="confirmTime"
      />
    </van-popup>
    <!-- 修改价格 -->
    <van-dialog
      v-model="price_show"
      :title="selected == '0'?cleanOrderType == 0?'清洁金额':'保洁金额':selected == '1'?'清洁金额':'保洁金额'"
      show-cancel-button
      :before-close="confirmDepart"
    >
      <van-cell-group>
        <van-field
          v-model="editDataForm.cleanAmount"
          :label="selected == '0'?cleanOrderType == 0?'清洁金额':'保洁金额':selected == '1'?'清洁金额':'保洁金额'"
          :placeholder="selected == '0'?cleanOrderType == 0?'请输入清洁金额':'请输入保洁金额':selected == '1'?'清洁金额':'保洁金额'"
        />
        <van-field
          v-model="editDataForm.remark"
          label="备注"
          placeholder="请输入备注"
        />
      </van-cell-group>
    </van-dialog>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { Toast } from 'vant';
import { GetBusinessCleanorderSum, GetBusinessCleanorderCleaningSum, GetBusinessCleanorderCleaningPage, GetBusinessCleanorderPage, GetBusinessCleanorderList, PostusinessCleanorderAmount, PostusinessCleaningorderAmount } from '@/api/api_staff';
export default {
    data() {
        return {
            time_show: false,
            price_show: false,
            times: null,
            cYearM: '',
            currentDate: new Date(),
            maxDate: new Date(),
            listLoading: false, //  上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, //  上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            listData: [], //列表数据
            comment_nums: '', //评论总数量
            totleNum: null,
            dataForm: {
                pageNum: 0,
                pageSize: 10000,
                hotelId: this.$route.query.hotelId,
                userId: this.$route.query.userId,
                status: '2',
                endTime: ''
            },
            editDataForm: {
                cleanAmount: null,
                remark: null,
                cleanOrderId: null,
                cleaningOrderId: null
            },
            allDataForm: {
                hotelId: this.$route.query.hotelId,
                endTime: ''
            },
            refData: null,
            cleanOrderType: null,
            selected: '1',
            key: 0,
            contentKey: 0,
            priceAll: 0
        };
    },
    filters: {
        filterAmount(price) {
            if (price) {
                return '￥' + price.toFixed(2);
            } else {
                return '￥' + 0.0;
            }
        }
    },
    created() {
        this.refData = this.userId || '';
        this.$store.commit('setHeader', '保洁统计');
        let c_time = dayjs().valueOf();
        let year = dayjs(c_time).year();
        let mouth = dayjs(c_time).month() + 1 < 10 ? '0' + (dayjs(c_time).month() + 1) : dayjs(c_time).month() + 1;
        this.cYearM = year + '年' + mouth + '月';
        this.dataForm.endTime = year + '-' + mouth;
        this.getCurrentInfo();
    },
    methods: {
        getCurrentInfo() {
            let _api = null;
            if (this.selected == 1) {
                _api = GetBusinessCleanorderPage;
                this.dataForm.pageNum = 0;
            } else if (this.selected == 2) {
                _api = GetBusinessCleanorderCleaningPage;
                this.dataForm.pageNum = 0;
            } else {
                this.allDataForm.endTime = this.dataForm.endTime;
                _api = GetBusinessCleanorderList;
            }
            _api(_api == GetBusinessCleanorderList ? this.allDataForm : this.dataForm).done((res) => {
                this.listData = [];
                this.priceAll = 0;
                this.totleNum = 0;
                if (res.data.length > 0) {
                    this.priceAll = res.data.reduce((totlePrice, item) => {
                        return (totlePrice += item.cleanAmount || 0);
                    }, 0);
                    this.totleNum = res.data.length;
                    this.dealArr(res.data);
                }
            });
            this.dataForm.pageNum++;
        },
        dealArr(arr) {
            if (arr.length == 0) {
                return;
            }
            let json = {};
            arr.map((item) => {
                item.myendTime = dayjs(item.endTime).format('YYYY-MM-DD');
                if (json[item.myendTime]) {
                    json[item.myendTime].push(item);
                } else {
                    json[item.myendTime] = [item];
                }
            });
            let d_arr = [];
            for (const key in json) {
                let d_obj = {};
                d_obj.time = key;
                d_obj.child = json[key];
                d_obj.status = false;
                d_obj.totlePrice = json[key].reduce((totle_cleanAmount, item) => {
                    return (totle_cleanAmount += item.cleanAmount || 0);
                }, 0);
                d_arr.push(d_obj);
            }
            ++this.contentKey;
            d_arr.find((item, i) => i == 0).status = true;
            this.listData = d_arr;
        },
        dealTime(timeC) {
            let year = dayjs(timeC).year();
            let mouth = dayjs(timeC).month() + 1 < 10 ? '0' + (dayjs(timeC).month() + 1) : dayjs(timeC).month() + 1;
            this.cYearM = year + '年' + mouth + '月';
            this.dataForm.endTime = year + '-' + mouth;
            this.getCurrentInfo();
        },
        confirmTime(event) {
            this.dealTime(dayjs(event).valueOf());
            this.time_show = false;
        },
        editPrice(_child) {
            this.editDataForm.cleanAmount = _child.cleanAmount || 0;
            this.editDataForm.cleanOrderId = _child.id;
            this.editDataForm.remark = _child.description;
            this.cleanOrderType = _child.cleanOrderType;
            this.price_show = true;
        },
        // PostusinessCleaningorderAmount:保洁
        // PostusinessCleanorderAmount:清洁
        confirmDepart(action, done) {
            if (action == 'confirm') {
                done(false);
                if (!this.editDataForm.cleanAmount) {
                    Toast('请输入保洁金额');
                    return;
                }
                let _api = null;
                if (this.selected == '0') {
                    if (this.cleanOrderType == '1') {
                        _api = PostusinessCleaningorderAmount;
                        delete this.editDataForm.cleanOrderId;
                    } else {
                        _api = PostusinessCleanorderAmount;
                        delete this.editDataForm.cleaningOrderId;
                    }
                } else if (this.selected == '1') {
                    _api = PostusinessCleanorderAmount;
                    delete this.editDataForm.cleaningOrderId;
                } else if (this.selected == '2') {
                    _api = PostusinessCleaningorderAmount;
                    delete this.editDataForm.cleanOrderId;
                }
                _api(this.editDataForm).done((res) => {
                    Toast(res.msg);
                    this.getCurrentInfo();
                });
                this.price_show = false;
            } else if (action == 'cancel') {
                done(false);
                this.price_show = false;
            }
        }
    }
};
</script>
<style scoped>
#cleaningTaskTop {
    font-size: 14px;
    padding: 15px 0;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    background-color: #f6f6f6;
}
.task-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    background: #fff;
}
.top-left {
    display: flex;
    align-items: center;
}
.totleAll {
    color: #9e9e9e;
    text-align: left;
    padding: 20px 15px;
    line-height: 30px;
    font-size: 14px;
}
.task-left {
    width: 70%;
    text-align: left;
}
.task {
    display: flex;
    align-items: center;
    line-height: 22px;
    margin: 10px;
    border-bottom: 1px solid #f2f2f2;
    font-size: 14px;
}
.task-right {
    width: 30%;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: right;
}
.task-right-top {
    display: flex;
    align-items: center;
    justify-content: right;
}
.typeStyle {
    border: 1px solid #ff7d41;
    font-size: 12px;
    color: #ff7d41;
}
.go {
    border: 1px solid #ff7d41;
    color: #ff7d41;
}
.back {
    border: 1px solid #02bc6c;
    color: #02bc6c;
}
.c_00 {
    color: #000;
}
.f18 {
    font-size: 18px;
    font-weight: bold;
    padding: 0 4px;
}
.f20 {
    font-size: 20px;
    font-weight: bold;
}
.f12 {
    color: #9e9e9e;
    font-size: 12px;
}
.task-list {
    background: #fff;
    overflow: auto;
    height: calc(100vh - 250px);
}
.cell_left {
    text-align: left;
    font-weight: bold;
}
.cell_left_bottom {
    font-weight: normal;
}
/deep/ .van-dialog__cancel {
    border: 1px solid #f2f2f2;
    border-radius: 10px;
}
/deep/ .van-dialog__footer {
    display: flex;
    overflow: hidden;
    -webkit-user-select: none;
    user-select: none;
    width: 90%;
    display: flex;
    margin: 20px auto;
    border-radius: 5px;
}
/deep/ .van-dialog__confirm {
    background: #ff7d41;
    color: #fff;
    border-radius: 10px;
}
/deep/ .van-dialog__header {
    padding: 20px 0;
}
/deep/ .van-cell--large .van-cell__label {
    font-size: 12px;
}
/deep/ .mint-navbar .mint-tab-item {
    padding: 18px 10px;
}
/deep/ .mint-tab-item-label {
    white-space: nowrap;
}
select {
    border: solid 1px #000;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
select::-ms-expand {
    display: none;
}
.mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    margin-bottom: 0;
    border-bottom: 3px solid #ff7d41;
}
</style>



