<template>
  <div id="invoice-top">
    <van-tabs
      v-model="selected"
      title-active-color='#ff7d41'
      color='#ff7d41'
    >
      <van-tab
        v-for="(item,i) in topTitle"
        :key='i'
        :title="item"
      ></van-tab>
    </van-tabs>
    <div v-if="selected == '0'">
      <!-- 日期范围 -->
      <div class="date">
        <div>日期范围</div>
        <div
          v-for="(item,i) in times"
          :key="i"
          class="b_f1"
          :style="{'color':(active == i)?'#048FE5':'#000'}"
          @click="chooseTime(i)"
        >
          {{ item }}
        </div>
      </div>
      <div
        v-if="active == '3'"
        class="time"
        @click="showTime"
      >
        <div>{{ dataTime }}</div>
      </div>
      <!-- 消耗品 -->
      <div
        v-if="consumeAll.length > 0"
        class="content"
      >
        <div class="f_w flex">
          <div class="l_c"></div>
          <div>消耗品</div>
        </div>
        <div class="center_flex center">
          <div class="ww_1">品类</div>
          <div class="ww_2">单价</div>
          <div class="ww_3">消耗数量</div>
          <div class="ww_4">小计</div>
        </div>
        <div
          v-for="(item,i) in consumeAll"
          :key='i'
          class="center_flex f14"
        >
          <div class="ww_1 c_39">{{ item.costName }}</div>
          <div class="ww_2">¥{{ Number(item.price).toFixed(2) }}</div>
          <div class="ww_3">{{ item.num }}</div>
          <div class="ww_4 c_39">¥{{ Number(item.price*item.num).toFixed(2) }}</div>
        </div>
        <div class="totle_right">合计&ensp;¥{{ Number(consumeTotle).toFixed(2) }}</div>
      </div>
      <!-- 布草洗涤 -->
      <div
        v-if="washAll.length > 0"
        class="content"
      >
        <div class="f_w flex">
          <div class="l_c b_c"></div>
          <div>布草洗涤</div>
        </div>
        <div class="center_flex center">
          <div class="ww_1">品类</div>
          <div class="ww_2">单价</div>
          <div class="ww_3">消耗数量</div>
          <div class="ww_4">小计</div>
        </div>
        <div
          v-for="(item,i) in washAll"
          :key='i'
          class="center_flex f14"
        >
          <div class="ww_1 c_39">{{ item.costName }}</div>
          <div class="ww_2">¥{{ Number(item.price).toFixed(2) }}</div>
          <div class="ww_3">{{ item.num }}</div>
          <div class="ww_4 c_39">¥{{ Number(item.price*item.num).toFixed(2) }}</div>
        </div>
        <div class="totle_right">合计&ensp;¥{{ Number(washTotle).toFixed(2) }}</div>
      </div>
      <div
        v-if="consumeAll.length == 0 && washAll.length == 0"
        style="margin:80px 0;"
      >
        暂无数据
      </div>
    </div>
    <div v-else-if="selected == '1'">
      <My-cleaningTask></My-cleaningTask>
    </div>
    <div v-else>
      <div
        class="add flex"
        @click="show_addOredit = true,editDataForm = {}"
      >
        <van-icon
          name="add"
          color="#FD7E43"
          style="padding-right:8px;"
        />添加{{ selected == '2'?'消耗品':'洗涤品' }}
      </div>
      <div class="cleaning-box">
        <div
          v-for="(item,i) in leaningAll"
          :key="i"
          class="cleaning-center"
          @click="editOrRemove(item)"
        >
          <div style="text-align:left;width:50%;">{{item.costName}}</div>
          <div class="flex">￥{{item.price}}/{{ item.unit }}
            <van-icon
              name="arrow"
              color="#B9B6BA"
              style="padding-left:8px;"
            />
          </div>
        </div>
      </div>
    </div>
    <!-- 选择开始、结束时间 -->
    <van-calendar
      v-model="time_show"
      type="range"
      :min-date="minDate"
      :max-date="maxDate"
      @confirm="onConfirm"
    />
    <!-- 展开删除，编辑 -->
    <van-popup
      position='bottom'
      :close-on-click-overlay='false'
      v-model="show_editOrRemove"
    >
      <div
        class="p10 c_e4"
        @click="removeItem"
      >删除</div>
      <div
        class="p10"
        @click="show_editOrRemove = false, show_addOredit = true"
      >编辑</div>
      <div
        style="height:6px;background:#f5f5f5;"
        @click="show_editOrRemove = false"
      ></div>
      <div
        class="p10 c_88"
        @click="show_editOrRemove = false"
      >取消</div>
    </van-popup>

    <!-- 展开添加、编辑 -->
    <van-dialog
      v-model="show_addOredit"
      :before-close="confirmEdit"
      :title="editDataForm.id?'编辑项目':'添加项目'"
      confirmButtonText='确定'
      confirmButtonColor='#fd7e43'
      cancelButtonColor='#7A7A7A'
      show-cancel-button
    >
      <van-field
        v-model="editDataForm.costName"
        label="名称"
        placeholder="请输入商品名称"
      />
      <van-field
        v-model="editDataForm.unit"
        label="单位"
        placeholder="请输入个、瓶、袋、条等单位"
      />
      <van-field
        v-model="editDataForm.price"
        label="价格"
        type='number'
        placeholder="请输入零售价"
      />
    </van-dialog>
  </div>
</template>
<script>
import { GetBusinessOrderroomCostSumByhotel, GetBusinessHotelCostQuery, PostBusinessHotelCostAdd, PostBusinessHotelCostEdit, PostBusinessHotelCostRemove } from '@/api/api_cleaning_or_goods';
import dayjs from 'dayjs';
import { Toast, Dialog } from 'vant';
import MyCleaningTask from '../Staff/cleaning-task.vue';
export default {
    components: { MyCleaningTask },
    props: ['propsData'],
    data() {
        return {
            selected: '0',
            leaningAll: [],
            unitAll: [],
            times: ['昨天', '本周', '本月', '自定义'],
            topTitle: ['成本统计', '保洁统计', '消耗品设置', '洗涤品设置'],
            consumeAll: [],
            washAll: [],
            editDataForm: {
                hotelId: this.$route.query.hotelId,
                hotelCostType: '2',
                id: null,
                costName: null,
                price: null,
                unit: null
            },
            active: '0',
            minDate: new Date(), //设置过期时间为当天
            maxDate: '',
            dataTime: null,
            consumeTotle: 0,
            washTotle: 0,
            time_show: false,
            show_editOrRemove: false,
            show_addOredit: false
        };
    },
    computed: {
        computerWidth() {
            return window.innerWidth / 4 + 'px';
        }
    },
    created() {
        this.cu_time = dayjs().subtract(1, 'year');
        this.minDate = new Date(this.cu_time);
        this.maxDate = new Date(dayjs());
    },
    watch: {
        selected(neww, olld) {
            this.$store.commit('setHeader', this.topTitle[neww]);
            if (neww == '0') {
                this.active = '0';
                this.s_date = dayjs().subtract(1, 'day').format('YYYY-MM-DD');
                this.e_date = dayjs().format('YYYY-MM-DD');
                this.dataTime = this.s_date + ' ' + '-' + ' ' + this.e_date;
                this.getTotle();
            } else if (neww == '2') {
                this.hotelCostType = '2';
                this.getInfo();
            } else if (neww == '3') {
                this.hotelCostType = '3';
                this.getInfo();
            }
        }
    },
    methods: {
        getTotle() {
            let data = {
                hotelId: this.$route.query.hotelId,
                startTime: this.s_date,
                endTime: this.e_date
            };
            GetBusinessOrderroomCostSumByhotel(data).done((res) => {
                this.consumeTotle = 0;
                this.washTotle = 0;
                this.consumeAll = [];
                this.washAll = [];
                if (res.data) {
                    this.consumeAll = res.data; // 消耗品
                    this.consumeAll.map((item) => {
                        this.consumeTotle += item.price * item.num;
                    });
                }
                if (res.order) {
                    this.washAll = res.order; // 洗涤品
                    this.washAll.map((item) => {
                        this.washTotle += item.price * item.num;
                    });
                }
            });
        },
        getInfo() {
            let data = {
                hotelId: this.$route.query.hotelId,
                hotelCostType: this.hotelCostType || '2'
            };
            GetBusinessHotelCostQuery(data).done((res) => {
                this.leaningAll = res.data;
            });
        },
        chooseTime(index) {
            this.active = index;
            this.e_date = dayjs().format('YYYY-MM-DD');
            if (index == '0') {
                this.s_date = dayjs().subtract(1, 'day').format('YYYY-MM-DD');
                this.dataTime = this.s_date + ' ' + '-' + ' ' + this.e_date;
                this.getTotle();
            } else if (index == '1') {
                this.s_date = dayjs().subtract(7, 'day').format('YYYY-MM-DD');
                this.dataTime = this.s_date + ' ' + '-' + ' ' + this.e_date;
                this.getTotle();
            } else if (index == '2') {
                this.s_date = dayjs().subtract(1, 'month').format('YYYY-MM-DD');
                this.dataTime = this.s_date + ' ' + '-' + ' ' + this.e_date;
                this.getTotle();
            } else {
                this.dataTime = this.s_date + ' ' + '-' + ' ' + this.e_date;
            }
        },
        showTime() {
            this.doble = [];
            this.time_show = true;
        },
        onConfirm(date) {
            const [start, end] = date;
            this.time_show = false;
            this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
            this.getTotle();
        },
        formatDate(date) {
            let doble = `${date.getFullYear()}-${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}-${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
            this.doble.push(doble);
            this.dataTime = this.doble[0] + ' ' + '-' + ' ' + this.doble[1];
            this.s_date = this.doble[0];
            this.e_date = this.doble[1];
            return `${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}/${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
        },
        editOrRemove(item) {
            this.currentInfo = item;
            this.editDataForm.id = item.id;
            this.editDataForm.costName = item.costName;
            this.editDataForm.price = item.price;
            this.editDataForm.unit = item.unit;
            this.show_editOrRemove = true;
        },
        removeItem() {
            this.show_editOrRemove = false;
            Dialog.confirm({
                title: this.currentInfo.costName,
                message: '确定删除?'
            })
                .then(() => {
                    PostBusinessHotelCostRemove({ id: this.currentInfo.id }).done((res) => {
                        this.getInfo();
                    });
                })
                .catch(() => {
                });
        },
        confirmEdit(action, done) {
            if (action == 'confirm') {
                if (!this.editDataForm.costName) {
                    Toast('请输入商品名称');
                    done(false);
                    return;
                }
                if (!this.editDataForm.unit) {
                    Toast('请输入个、瓶、袋、条等单位');
                    done(false);
                    return;
                }
                if (!this.editDataForm.price) {
                    Toast('请输入零售价');
                    done(false);
                    return;
                }
                if (this.editDataForm.id) {
                    this.editDataForm_ = {
                        id: this.editDataForm.id,
                        costName: this.editDataForm.costName,
                        price: this.editDataForm.price,
                        unit: this.editDataForm.unit
                    };
                    PostBusinessHotelCostEdit(this.editDataForm_).done((res) => {
                        done(false);
                        this.show_addOredit = false;
                        this.getInfo();
                    });
                } else {
                    this.addDataForm = {
                        hotelId: this.$route.query.hotelId,
                        hotelCostType: this.hotelCostType || '2',
                        costName: this.editDataForm.costName,
                        price: this.editDataForm.price,
                        unit: this.editDataForm.unit
                    };
                    PostBusinessHotelCostAdd(this.addDataForm).done((res) => {
                        done(false);
                        this.show_addOredit = false;
                        this.getInfo();
                    });
                }
            } else if (action == 'cancel') {
                done(false);
                this.show_addOredit = false;
            }
        }
    }
};
</script>
<style scoped>
#invoice-top {
    overflow: auto;
    height: 90%;
    position: fixed;
    top: 60px;
    left: 0;
    right: 0;
    font-size: 15px;
    color: #000;
}
.add {
    color: #fd7e43;
    background: #fff;
    margin: 12px 0;
    font-size: 16px;
    padding: 8px 0;
}
.cleaning-box {
    background: #fff;
    color: #000;
    font-size: 15px;
    position: fixed;
    left: 0;
    right: 0;
    height: calc(100% - 170px);
    overflow: auto;
}
.cleaning-center {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    border-bottom: 1px solid #f8f6f9;
}
.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}
.p10 {
    padding: 10px;
    border-bottom: 1px solid #f8f6f9;
    color: #000;
}
.c_e4 {
    color: #e46a69;
}
.c_88 {
    color: #888;
}
.f_w {
    font-weight: bold;
    justify-content: flex-start;
}
.f14 {
    font-size: 14px;
}
.center_flex {
    display: flex;
    width: 100%;
    justify-content: space-between;
    color: #393939;
    background: #fff;
    padding: 8px 16px;
    box-sizing: border-box;
    border-bottom: 1px solid #f1f1f1;
}
.center {
    background: #f0eff2;
    color: #000;
    font-weight: bold;
}
.ww_1 {
    width: 32%;
    text-align: left;
}
.ww_2 {
    width: 24%;
    text-align: left;
}
.ww_3 {
    width: 22%;
    text-align: center;
}
.ww_4 {
    width: 22%;
    text-align: right;
}

/deep/ .van-dialog__header {
    font-weight: bold;
    color: #000;
    padding: 20px 0 10px 0;
}
/deep/ .van-cell {
    align-items: center;
}
/deep/ .van-field__value {
    background: #f0eff2;
    color: #000;
    border-radius: 4px;
}
/deep/ .van-field__label {
    color: #000;
    font-weight: bold;
    width: 5.5em;
    text-indent: 1em;
}
/deep/ .van-field__control {
    text-indent: 1em;
    padding: 4px 0;
}
/deep/ .van-cell::after {
    border: none;
}
/deep/ .d_van_tab {
    border-radius: 20px;
    height: 20px;
    background: #efeff4;
    color: #000;
    font-size: 12px;
}
/deep/ .van-tabs__nav--card {
    border: none;
}
/deep/ .van-tabs__nav--card .van-tab {
    border-right: none;
}
/deep/ .van-tabs__nav--card .van-tab.van-tab--active {
    background: #efeff4;
    color: #148fdc;
}
/deep/ .van-tabs--card > .van-tabs__wrap {
    margin-top: 10px;
    background: #fff;
    padding: 20px 0 10px;
}
.date {
    display: flex;
    background: #fff;
    padding: 10px 0;
    font-size: 14px;
    width: 100%;
    box-sizing: border-box;
    justify-content: space-around;
    margin-top: 10px;
}
.b_f1 {
    background: #f0eff2;
    width: 14%;
    border-radius: 20px;
}
.time {
    background: #fff;
    display: flex;
    justify-content: center;
    color: #000;
    padding: 10px 0;
}
.content {
    text-align: left;
    background: #fff;
    margin: 10px 0 0;
    color: #000;
    font-size: 15px;
}
.content_flex {
    display: flex;
    justify-content: space-between;
    padding: 10px;
}
.l_c {
    height: 16px;
    background: red;
    width: 4px;
    margin: 10px;
}
.b_c {
    background: #2e7bf7;
}
.bg {
    background: #f1f1f1;
    margin: 0 8px;
}
.c_39 {
    color: #393939;
    font-weight: bold;
}
.totle_right {
    text-align: right;
    color: #000;
    font-weight: bold;
    padding: 10px 16px;
    font-size: 13px;
}
</style>



